import {
	request
} from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			packet:[],
			curPacket:0,
			express:[],
		};
	},
	onLoad(options){
		let id = options.id;
		this.getInfo(id);
	},
	methods:{
		getInfo(id){
			let that = this;
			request({
				url: 'index.php?s=/wap/order/apiOrderExpress',
				data: {
					order_id: id
				},
			}).then(function(res){
				that.packet = res.data.data.order.goods_packet_list;
				that.getExpress();
			});
		},
		getExpress(){
			let that = this;
			let express_id = that.packet[that.curPacket].express_id;
			request({
				url: 'index.php?s=/wap/order/ApiGetOrderGoodsExpressMessage',
				data: {
					express_id: express_id
				},
				method:"POST"
			}).then(function(res){
				if(res.data.code==1){
					that.express = res.data.list;
				}
			});
		},
		changePacket(i){
			this.curPacket = i;
			this.getExpress();
		}
	}
}
